import React, { useState, useEffect, useCallback } from 'react';
import { AnimatePresence, motion } from 'framer-motion';
import '../styles/main.scss';
import { projects } from "../components/data";

function Main({scrollToSection}) {

  const [isHovered, setIsHovered] = useState(false);
  const fonts = [
    "'Fira Code', monospace",
    "'Roboto', sans-serif",
    "'Lato', sans-serif",
    "'Montserrat', sans-serif",
    "'Open Sans', sans-serif",
    "'Poppins', sans-serif",
    "'Playfair Display', serif",
    "'Raleway', sans-serif",
    "'Nunito', sans-serif",
    "'Merriweather', serif",
    "'Quicksand', sans-serif",
    "'Josefin Sans', sans-serif",
    "'Rubik', sans-serif",
    "'Mukta', sans-serif",
    "'Bebas Neue', sans-serif",
    "'Noto Sans', sans-serif"
  ];
  const colors = ['var(--font-color)', 'var(--red)'];
  const decorations = ['underline', 'line-through', 'none'];
  const transforms = ['none','uppercase','unset']


  const [font, setFont] = useState(fonts[0]);
  const [color, setColor] = useState(colors[0]);
  const [decoration, setDecoration] = useState(decorations[0]);
  const [transform, setTransforms] = useState(transforms[0]);

  const [isAnimating, setIsAnimating] = useState(false);

  const startAnimation = useCallback(() => {
    setFont(fonts[Math.floor(Math.random() * fonts.length)]);
    setColor(colors[Math.floor(Math.random() * colors.length)]);
    setDecoration(decorations[Math.floor(Math.random() * decorations.length)]);
    setTransforms(transforms[Math.floor(Math.random() * transforms.length)]);
    setIsAnimating(true);
  }, [fonts, colors, decorations, transforms]);

  useEffect(() => {
    if (!isAnimating) {
      const interval = setInterval(() => {
        startAnimation();
      }, 2500);

      return () => clearInterval(interval);
    }
  }, [isAnimating, startAnimation]);

  useEffect(() => {
    setIsAnimating(false);
  }, [font, color, decoration, transform]);

  const variants = {
    initial: { opacity: 0, y: 10 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: 0, x: 0, filter: "blur(8px)", scale: 2, position: "absolute" },
  };

  const letterVariants = {
    initial: { opacity: 0, y: 10, filter: "blur(8px)" },
    animate: { opacity: 1, y: 0, filter: "blur(0px)" },
  };

  const style = {
    color: color,
    fontFamily: font,
    textDecoration: decoration,
    textTransform: transform,
  };

  const name = 'Samuel';

  return (
    <div className='main-page'>
      <div className='main-left-side'>
      {projects.slice(0,6).map((project) => (
        <div key={project.id} className={`box box${project.id} `}>
          <img src={require(`../images/pitcImages/${project.images[project.id]}`)} alt={project.name} />
        </div>
        ))}
        <button className='view-projects' onClick={() => scrollToSection("work")}><h4>View Projects</h4></button>
      </div>
      <div className='main-right-side'>
        <AnimatePresence>
          <motion.h1
            key={`${font}-${color}-${decoration}`}
            className='main-name first'
            style={style}
            initial="initial"
            animate="animate"
            exit="exit"
            variants={variants}
            transition={{ duration: 0.5, ease: "easeInOut", type: "spring", stiffness: 100, damping: 10 }}
          >
            {name.split("").map((letter, index) => (
              <motion.span
                key={`${font}-${letter}-${index}`}
                variants={letterVariants}
                transition={{ delay: index * 0.08, duration: 0.3 }}
                className="inline-block"
              >
                {letter}
              </motion.span>
          ))}
          </motion.h1>
       </AnimatePresence>
          <div className='last-container'><h1 className='main-name last'>Akintomide</h1></div>
        
      </div>
      <div style={{ display: "flex", justifyContent: "center" }}>
      </div>
    </div>
  );
}

export default Main;

