    icon: "artex.png", 
    information:`<h4 style="display: flex; margin: 30px 0 10px;">1.0 Introduction</h4>
    <p>The robot, named ARTEx (All-terrain Robotic Transportation and Exploration System), is specifically designed for deployment in the medical industry, enabling the swift delivery of medical items to critical areas. Inspired by initiatives like Zipline's operations in Rwanda, ARTEx aims to overcome the limitations of gliding drones, particularly in urban settings such as London with numerous high-rise buildings. Unlike traditional gliding drones, this system avoids dropping medical packages via parachute during flight, minimizing the risk of damage or getting stuck in inaccessible locations such as trees.</p>
    <h4 style="margin: 15px 0 10px;">1.5 Legal, Social, Ethical and Professional Issues</h4>
    <h5>1.5.1 Legal Issues</h5>
    <p>There are numerous legal issues regarding piloting drones in the UK. As of 2024, the Civil Aviation Authority of the United Kingdom (CAA), the nations governing body for aviation matters, has implemented a comprehensive and robust set of drone laws. A long list of the laws can be found on the metropolitan police website which specify altitude limitations, age restrictions, and licenses to fly. Figure 2 documents just a few of these laws. There are also major concerns regarding drones/devices and cameras causing some to worry about who is watching. Liability issues in the case of accidents, property damage, or injuries caused by the drone's operation are also another concern. This alludes to why some laws were put in place about how many people a drone can be piloted over along with the reasoning for height restrictions.</p>
    <h5>1.5.2 Social Issues</h5>
    <p>Unlike legal issues, the social issues of piloting a drone and a hovercraft have several crossovers. For example, both types of vehicles can be very loud due to the high-intensity motors and may disturb people, so there is the problem of noise pollution.</p>
    <h5>1.5.3 Ethical Issues</h5>
    <p>Regarding this project, there are few ethical issues. Privacy concerns and data security arent an issue with the ARTEx as it wasnt built to read or collect data on people. This iteration of ARTEx does not use cameras. At most, the integration of a camera on the ARTEx will aid with obstacle avoidance. When piloting the ARTEx it would rarely be at eye level. It will usually be on the ground or in the sky. There are environmental considerations as piloting the ARTEx could cause habitat disturbance or wildlife disruption.</p>
    <h5>1.5.4 Professional Issues</h5>
    <p>Drones and Hovercrafts are complex vehicles. Combining them will be no easy feat. There are a lot of minor intricacies that one must work out and tweak in order to get a well-performing vehicle. For example, when creating a drone, how someone connects to and controls the drone is a massive aspect. If there is a lot of delay and buffering between each input, it could lead to crashes when flying low. ARTEx could potentially crash into buildings or trees if sensors and actuators arent calibrated which could cause safety issues and/or affect whether a package is delivered in time or at all.</p>
    <h4 style="margin: 15px 0 10px;">1.6 Report Structure</h4>
    <p>This report will cover the research and findings throughout the progression and development of the ARTEx. It will initially cover the scientific and mathematical side of making the ARTEx functional, then touch on the build and development of the ARTEx over time.</p>`,
