      name: "Bank Code", 
      description: "A Java Bank to allow user to carry out the typical functionality of a normal bank system",
      imageCode:"bank",
      icon: "bank.png", 
      information:`<h2 style={{position:"relative", display:"flex", justifyContent:"end" }}>Instructions</h2>
      Run the Code and you will be in the main menu. The first thing you will see is the is a welcome message and then a small section that says type followed by commands in square brackets that you can type to carry out commands that you would like to run such as New, Returning, Display, Delete and exit. These commands are not case sensitive. Instructions on how to use each section will be written below.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Creating a new account</h4>
      To create a new account, type new in the main menu (where you see New in square brackets). When typing new capitalisation doesnt matter. You will then be asked to type the first name of the account holder you are making the account for, followed by an output asking for to type the last name of the account holder and finally their address. Make sure not to add any spaces before or after the account holders first and last name as it will mess up the formality.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Accessing created accounts</h4>
      To access an already created account, type returning in the main menu (where you see Returning in square brackets). You will then be asked to enter the account number of the account which you wish to access. If the account number you have entered does not exist then this will not work, and you will be returned to the main menu.
      Once you have entered the account number of the account that you wish to access you will be met with another UI that displays options to type either Deposit, Withdraw, View, Change, Close or Exit.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Depositing and withdrawing money</h4>
      To deposit/withdraw money, type deposit/ withdraw in the account menu. You will then be met with a message to asking you to enter the amount of money you would like to deposit/withdraw. You will then be able to enter any amount up to 2 decimal places and the funds will be deposited into the account. Not the warning message in brackets saying Dont Include Symbols. If any values are used that are not numbers or a full stop (to indicate that you are depositing currency smaller than a dollar into the account) then the value inputted will not be accepted.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Viewing last 6 transactions</h4>
      To view last 6 transactions that were carried out in the account, type view in the account menu. This will display your last 6 transactions by size (largest to smallest). It will also show details of the transactions such as whether it was deposited or withdrawn, the amount that was deposited/ withdrawn, and the date which the transaction was carried out.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Changing account information</h4>
      To change user specific account information, type change in the account menu. You will then be met with a menu that will give you the option to change the account holders first name, last name or address. To only change the first name type first, last name type last, both last and first name type both or address type address. Depending on what option was chosen you will then be asked to
      enter the new desired values. Note that you will not be able to change the account number, balance, opening date or status.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Closing account</h4>
      To close the account so that it can be deleted, type close in the account menu. If the account balance is $0 then you will be given two options to either type close to close the account or exit to cancel the process. If you type close then the account will then be closed and you will be returned to the main menu or else if you type exit the process will be cancelled and you will be returned to the main menu.
      <br /> <br /> On the other hand, if the balance in the account is more than $0 you will be given 3 options. The first option will be to withdraw the remaining funds. If that option is chosen, then the remaining funds will be withdrawn, and the account will be closed. The second option will be to transfer the remaining funds to a different account. If that option is chosen, then you will be asked to enter the account number of the account which you wish to transfer the remaining funds to. If the account number entered is not existent then the process wont work and you will be asked if you wish to continue, you can then type yes or no depending on what you wish to do. If the account entered is initiated, then the remaining funds will be transferred to that account and the current account you are in will be closed and you will be transferred to the main menu. The third option is to exit the process. If that option is selected then the process will be cancelled and you will be brought back to the main menu.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Logging out of account</h4>
      To logout of the account, type exit and you will be brought back to the main menu. All prior changes if any were made will be automatically saved.
      Displaying created accounts
      To see all created account and their general information, type display in the main menu. The general information that will be displayed for each account includes Account Number, Account Holder Name, Account Holder Address, Account Balance, Account Opening Date and Account Status. After all account general information is shown you will be returned to the main menu.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Deleting Accounts</h4>
      To delete an account that has been created, type delete. To delete an account the account must initially be closed. If you attempt to close an open account, then the process will not work, and you will be brought back to the main menu. On the other hand, if the account status of the account you wish to delete it Closed then the account will be deleted.
      <h4 style={{display:"flex", margin:"30px 0 10px"}}>Exiting the program</h4>
      To exit the program, type exit in the main menu. You will then be met with a second message to confirm whether you would like to exit the program at which point you would type yes to confirm or no to cancel. Note that all data inputted into the program will not be saved once the program is exited.`,
