import React, {useRef, useState, useEffect} from 'react';
import Main from './main';
import Skills from './Skills';
import Projects from './projects';
import FAQ from './FAQ';
import Footer from './Footer';

import style from '../styles/home.module.scss';

import { useInView, useScroll, motion, useTransform } from 'framer-motion';

function Home({setCurrentPage, refs, scrollToSection}) {
  const [showFooter, setShowFooter] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrolled = window.scrollY;
      const windowHeight = window.innerHeight;
      if (scrolled > windowHeight * 0.7) {
        setShowFooter(true);
      } else {
        setShowFooter(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);
  
  const { scrollYProgress } = useScroll({
    target: refs.main,
    offset: ["start start", "end 100vh"]
  }) 
  const scale = useTransform(scrollYProgress, [0, 1], [1, 0.2]);
  const rotate = useTransform(scrollYProgress, [0, 1], [0, -15]);

  return (
    <div ref={refs.main} className={style.page}>  

      <div data-section="main" className={style.mainContainer}> 
        <motion.div className={style.main} style={{scale, rotate}}>
          <Main scrollToSection={scrollToSection}/>
        </motion.div>
      </div> 
      
      <div className={style.outerContainer}>
        <div data-section="skills" className={style.skills} ref={refs.skills}>
          <Skills/>
        </div>
      
        <div ref={refs.work} data-section="work" className={style.projects} >
          <Projects setCurrentPage={setCurrentPage}/>
        </div>
        <div className={style.faq} style={{zIndex:9}} data-section="faq" ref={refs.faq}>
          < FAQ/>
        </div>
      </div>

      {showFooter && (<Footer />)}
    <div data-section="contact" style={{height:"1px"}} ref={refs.contact}></div>
      
      
    </div>
  );
}

export default Home;
