import React, { useState, useEffect, useRef } from "react";
import { motion, AnimatePresence } from 'framer-motion';

import Header from './pages/Header';
import Home from './pages/Home';
import ProjectDetails from './pages/projectDetails';
import Footer from './pages/Footer'

import Background from "./components/background";
import Intro from "./pages/intro";

import './App.scss';

function App() {
  const [currentPage, setCurrentPage] = useState({ page: 'intro' });
  const [currentSection, setCurrentSection] = useState({ section: 'main' });
  const [isAnimation, setAnimation] = useState(false);
  const [showFooter, setShowFooter] = useState(false);

  const pageVariants = {
    project: {
      initial: { opacity: 0, scale: 0.5},
      animate: { opacity: 1, scale: 1 },
      exit: {x: "-100vw"},
    },
    home: {
      initial: { opacity: 0, x: "100vw"},
      animate: { opacity: 1, scale: 1, x: 0 },
      exit: { opacity: 0, scale: 0.8 }
    }
  };  

  const section1Ref = useRef(null);
  const section2Ref = useRef(null);
  const section3Ref = useRef(null);
  const section4Ref = useRef(null);
  const section5Ref = useRef(null);

  const sectionRefs = {
    main: section1Ref,
    skills: section2Ref,
    work: section3Ref,
    faq: section4Ref,
    contact: section5Ref,
  };

  const scrollToSection = (section) => {
    const sectionRef = sectionRefs[section];
    if(sectionRef && sectionRef.current){
      const offset = section === "main" ? -50 : section === "contact" ? -100 : 10;
      const topPos = sectionRef.current.getBoundingClientRect().top + window.scrollY;
      window.scrollTo({ top: topPos + offset, behavior: "smooth" });
      //sectionRef.current.scrollIntoView({behavior: "smooth"});
    }
  };

  useEffect(() => {
    const handleScroll = () => {
      const scrolled = window.scrollY;
      const windowHeight = window.innerHeight;
      if (scrolled > windowHeight * 0.7) {
        setShowFooter(true);
      } else {
        setShowFooter(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const section = entry.target.getAttribute('data-section');
            setCurrentSection({ section });
          }
        });
      },
      { threshold: 0.4 } 
    );

    Object.values(sectionRefs).forEach((ref) => {
      if (ref.current) {
        observer.observe(ref.current);
      }
    });

    return () => {
      Object.values(sectionRefs).forEach((ref) => {
        if (ref.current) {
          observer.unobserve(ref.current);
        }
      });
    };
  }, [sectionRefs]);

  const renderPage = () => {
    switch (currentPage.page) {
      case 'intro':
        return <Intro setAnimation={setAnimation} setCurrentPage={setCurrentPage}/>;
      case 'home':
        return <Home setCurrentPage={setCurrentPage} refs={sectionRefs} scrollToSection={scrollToSection}/>;
      case 'project':
        return < ProjectDetails setCurrentPage={setCurrentPage} projectId={currentPage.projectId} />
       default:
        return <Home setCurrentPage={setCurrentPage} refs={sectionRefs}/>;
    }
  };
  return (
    <>
    {isAnimation &&(<>
      <motion.div
        className="swipe"
        initial={{x: 0 }}
        animate={{y: "110vh" }}
        transition={{delay: 1, type: "ease-in-out", duration: 0.8, ease: [0, 0.71, 1.01, 0.2 ]}}
      />
      <motion.div
        className="swipe"
        initial={{x: 0 }}
        animate={{x: "-110vw" }}
        transition={{delay: 1, type: "ease-in-out", duration: 0.8, ease: [0, 0.71, 1.01, 0.2] }}
      /></>)}
      <Background />
      <Header setCurrentPage={setCurrentPage}  setCurrentSection={setCurrentSection} scrollToSection={scrollToSection}  currentSection={currentSection.section}/>
      <div>
      <AnimatePresence mode="wait">
      <motion.div
            key={currentPage.page} 
            variants={pageVariants[currentPage.page]}
            initial="initial"
            animate="animate"
            transition={{duration: 0.8, delay: 0.3, ease: [0, 0.71, 0.2, 1.01]}}
            exit="exit"
            >{renderPage()} </motion.div>            
        </AnimatePresence>
      </div>
      
    </>
  );
}

export default App;
 