import React, { useEffect } from 'react';

const Background = () => {
  
  useEffect(() => {
    function getRandomColor() {
      const colors = ["#1A1E23", "#F4F6F6"]; // dark and light
      const randomIndex = Math.floor(Math.random() * colors.length);
      return colors[randomIndex];
    }
  
    const background = getRandomColor();
    document.body.style.setProperty("--background", background);
    document.body.style.setProperty("--font-color", background === "#1A1E23" ? "white" : "black");
    document.body.style.setProperty("--secondary-color", background === "#1A1E23" ? "#32363A" : "#DCDEDF");
    document.body.style.setProperty("--tertiary-color", background === "#1A1E23" ? "#4A4E52" : "#C4C6C7");
    document.body.style.setProperty("--quaternary-color", background === "#1A1E23" ? "#636669" : "#ABAEB0");
    document.body.style.setProperty("--contrast", background === "#1A1E23" ? "0%" : "100%");
  }, []);
  return null; 
};

export function setWhite() {
  document.body.style.setProperty("--background", "#F4F6F6");
  document.body.style.setProperty("--font-color", "black");
  document.body.style.setProperty("--secondary-color", "#DCDEDF");
  document.body.style.setProperty("--tertiary-color", "#4A4E52");
  document.body.style.setProperty("--quaternary-color", "#636669");
  document.body.style.setProperty("--contrast", "0%");
}

export function setBlack() {
  document.body.style.setProperty("--background", "#1A1E23");
  document.body.style.setProperty("--font-color", "white");
  document.body.style.setProperty("--secondary-color", "#32363A");
  document.body.style.setProperty("--tertiary-color", "#C4C6C7");
  document.body.style.setProperty("--quaternary-color", "#ABAEB0");
  document.body.style.setProperty("--contrast", "100%");  
}

export default Background;
