import React from 'react';
import { motion } from 'framer-motion';
import {projects} from "../components/data";

import style from '../styles/project.module.scss'

function Projects({setCurrentPage}) {

  const handleProjectClick = (projectId) => {
    setCurrentPage({ page: 'project', projectId });
  };

  const getImagePath = (icon) => {
    return require(`../images/thumbnails/${icon}`);
  };

  return (
    <div className={style.page}>
      <div className={style.title}>
        <h1>Features Projects</h1><h8>[003]</h8></div>

      <div className={style.projects}>
        {projects.map((project) => (
          <motion.div key={project.id} className={style.items} onClick={() => handleProjectClick(project.id)}>

          <div className={style.imageContainer} ><img src={getImagePath(project.icon)} alt={project.name} /></div>
          <h3>{project.name}</h3>
          <p>{project.description}</p>
          </motion.div>
        ))}
      </div>     
    </div>       
  );
}

export default Projects;
 
