import React, { useState, useEffect } from 'react';
import { motion, useScroll, useSpring } from "framer-motion";
import Background from '../components/backgroundChage';
import style from "../styles/header.module.scss";


function Header({setCurrentPage, setCurrentSection, scrollToSection, currentSection }) {

  const [currentTime, setCurrentTime] = useState(new Date().toLocaleTimeString());
  const { scrollYProgress } = useScroll();
  
  const scaleX = useSpring(scrollYProgress, {
    stiffness: 100,
    damping: 30,
    restDelta: 0.001
  });

  useEffect(() => {
    const timerId = setInterval(() => {
      setCurrentTime(new Date().toLocaleTimeString());
    }, 1000);
    return () => clearInterval(timerId); 
  }, []);

  const handlePage = () => {
    setCurrentPage({ page: 'home'});
  };

  const handleSectionClick = (section) => {
    setCurrentSection({ section });
    scrollToSection(section);
    if (section === 'contact') {
      setTimeout(() => {
        scrollToSection(section);
      }, 250); 
    }
  };

  return (
  <header>
    <div className={style.container}>
      <div className={style.titleContainer} onClick={() => handlePage()}>
        <h3 className={style.title}>Samuel Akintomide</h3>
        <h4 className={style.title}>Robotics Engineer, Developer and Designer</h4>
      </div>
      <div>
        <div className={style.time}>{currentTime}</div>
        <div className={style.buttonContainer}>
          <button className={`${style.button} ${currentSection === 'main' ? style.active : ''}`} onClick={() => handleSectionClick("main")}>Home</button>
          <button className={`${style.button} ${currentSection === 'skills' ? style.active : ''}`} onClick={() => handleSectionClick("skills")}>Skills</button>
          <button className={`${style.button} ${currentSection === 'work' ? style.active : ''}`} onClick={() => handleSectionClick("work")}>Projects</button>
          <button className={`${style.button} ${currentSection === 'faq' ? style.active : ''}`} onClick={() => handleSectionClick("faq")}>FAQ</button>  
          <button className={`${style.button} ${currentSection === 'contact' ? style.active : ''}`} onClick={() => handleSectionClick("contact")}>Contact</button>  
        </div>
      </div>
    </div>
    <motion.div className={style.progressBar} style={{scaleX }} />
    {/*<motion.div className="header-progress-bar" style={{scaleX: scrollYProgress }} />*/}
    <Background />
  </header>

  );
}

export default Header;
