import React, { useEffect, useState } from "react";
import { motion, AnimatePresence} from "framer-motion";
import "../App.scss";
import intro from "../styles/intro.module.scss";


function Intro({setAnimation, setCurrentPage}) {
  const [showFirstAnimation, setShowFirstAnimation] = useState(true);
  const [showSecondAnimation, setShowSecondAnimation] = useState(false);
  const [showThirdAnimation, setShowThirdAnimation] = useState(false);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const isMobile = windowWidth <= 675;

  useEffect(() => {
    const handleWindowResize = () => {
      setWindowWidth(window.innerWidth);
    };
    window.addEventListener("resize", handleWindowResize);
    return () => {
      window.removeEventListener("resize", handleWindowResize);
    };
  }, []);

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowFirstAnimation(false);
    }, 1200); // 0.5s (initial animation) + 0.7s delay

    return () => clearTimeout(timer);
  }, []);

  const handleFirstAnimationComplete = () => {
    setShowSecondAnimation(true);
  };

  const handleSecondAnimationComplete = () => {
    setShowThirdAnimation(true);
  };

  const handleAnimationComplete = setTimeout(() => {
    setCurrentPage({ page: 'home' }); setAnimation(true);
  }, 5000);

  return (
      <div className={intro.loading}>
        <AnimatePresence >
          {showFirstAnimation && (
            <motion.div
              className={intro.load}
              key="image"
              initial={{ scale: 0 }}
              animate={{ rotate: [0, 360], scale: [0, 1],  }}
              transition={{ delay: 0.2, type: "spring", stiffness: 260, damping: 20, duration: 0.5 }}
              exit={{ opacity: 0, transition: { duration: 0.5, stiffness: 260, damping: 20 } }}
              onAnimationComplete={handleFirstAnimationComplete}>
              <img src={require("../images/shoto.gif")} alt="shoto" />
            </motion.div>
          )}
        </AnimatePresence>

        <AnimatePresence>
          {showSecondAnimation && (
            <motion.div
              className={intro.display}
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1, type: "spring", stiffness: 260, damping: 20, duration: 0.5 }}
              exit={{ opacity: 0, transition: { duration: 0.5, stiffness: 260, damping: 20 } }}
              onAnimationComplete={handleSecondAnimationComplete}>
              <h1>Samuel Akintomide</h1>
              <h4 className="intro-h4">Robotics Engineer, Developer, and Designer</h4>
            </motion.div>
          )}
        </AnimatePresence>
      
        <AnimatePresence mode="wait" >
          {showThirdAnimation && (
            <motion.div
              className={intro.swipe}
              initial={{x: "-115vw" }}
              animate={{x: 0  }}
              transition={{delay: 1, type: "ease-in-out", duration: 0.5 }}
            />
          )}
        </AnimatePresence>
      </div>
  );
}

export default Intro;
