import React, {useRef} from 'react';
import style from '../styles/faq.module.scss';

function FAQ() {

  return (
    <div className={style.page}>
      <div className={style.title}>
        <h1>FAQ</h1><h8>[004]</h8>
     </div>
      <div className={style.textContainer}>
        <h4>Who are you?</h4>
        <p>Im a robotics engineer with a robust set of skills. I have experienced in a wide range of 
        IT related fields. Skills which have trained and honed in secular society and alone at home 
        through practice and repetition. </p>
        <h4 >What are you aiming for?</h4>
        <p>My dream is to work for a robotics company, aiding in the ever quick and changing environment 
        that is AI and Robots. </p>
        <h4 >Do you have any passions?</h4>
        <p>I have a passion or addiction rather for developing software and coding. I find building 
        and manipulating different systems intriguing. I also struggle to stop working and go to 
        sleep but Thomas Edison never slept and neither did Newton so why should I?
        <br/><br/>I love to problem solve. My favourite subject remains math with physics in a close second. 
        Biology comes third as I would like to on day have the freedom to dedicate some time to 
        medical research, implementing skills from robotics to allow the unable to become able again.
        Building has been prevalent throughout my life. Once again, being able to design and construct 
        something is amazing.</p></div>
    </div>
  );
}

export default FAQ;
