import { setBlack, setWhite } from './background';
import "../styles/colour.scss"

function BackgroundChange() {
  const whiteBack = () => {
    setWhite();
  };

  const blackBack = () => {
    setBlack(); 
  };

  return (
    <div className="colourPanel">
        <ul><li className="boxW"/>
            <button className="buttonC" onClick={whiteBack}>Light</button></ul>
        <ul><li className="boxB"/> 
            <button className="buttonCC" onClick={blackBack}>Dark</button></ul>
    </div>
  );
}

export default BackgroundChange;
